import React, { Component } from "react";

class index extends Component {
  constructor(props) {
    super(props);
  }
  componentDidMount() {
    console.log(this.props.matchDetails);
  }

  render() {
    if (!this.props.matchDetails) return <div>Loading...</div>;
    return (
      <div>
        <h2 className="team-title">{`.::${this.props.matchDetails[0].batting_team} - ${this.props.matchDetails[0].bowling_team}::.`}</h2>
        <table className="table table-bordered">
          <thead>
            <tr className="heading-main">
              <th>inning</th>
              <th>batting_team</th>
              <th>bowling_team</th>
              <th>over</th>
              <th>ball</th>
              <th>batsman</th>
              <th>non_striker</th>
              <th>bowler</th>
              <th>is_super_over</th>
              <th>wide_runs</th>
              <th>bye_runs</th>
              <th>legbye_runs</th>
              <th>noball_runs</th>
              <th>penalty_runs</th>
              <th>batsman_runs</th>
              <th>extra_runs</th>
              <th>total_runs</th>
              <th>player_dismissed</th>
              <th>dismissal_kind</th>
              <th>fielder</th>
            </tr>
          </thead>
        </table>
      </div>
    );
  }
}

export default index;
